import { useState, useRef, useCallback, SyntheticEvent, useEffect } from "react";

import useDispatchAction from "./useDispatchAction";

import { SliderOrientation } from "types";

type TextContent = string;

export const useManageInput = (orientation: SliderOrientation, helper: Function) => {
    const [textContent, setTextContent] = useState<TextContent>("");
    const inputRef = useRef<HTMLInputElement>(null);
    const { setQuery } = useDispatchAction();

    const inputClickHandler = useCallback((event: { key: string }) => {
        if (event.key === "Enter") {
            inputRef.current && inputRef.current!.value && setTextContent(inputRef.current!.value);
        }
    }, []);
    const searchHandler = useCallback(
        (e: SyntheticEvent) => {
            e.stopPropagation();
            e.preventDefault();
            inputRef.current && inputRef.current!.value && setTextContent(inputRef.current!.value);
            if (orientation === "horizontal") {
                helper();
            }
        },
        [helper, orientation]
    );

    const updateTextContent = useCallback(
        (newText: TextContent) => {
            const updatedTextContent = textContent + " " + newText;
            setTextContent(updatedTextContent);
        },
        [textContent]
    );

    const updateInput = useCallback(
        (newText: TextContent) => {
            const updatedTextContent = textContent + " " + newText;
            inputRef.current!.value = updatedTextContent;
        },
        [textContent]
    );

    useEffect(() => {
        if (textContent) {
            setQuery(textContent);
        }
    }, [setQuery, textContent]);

    return { searchHandler, inputClickHandler, inputRef, setTextContent, textContent, updateInput, updateTextContent };
};

export default useManageInput;
